"use client";

import * as React from "react";
import Link from "next/link";
import Image from "next/image";
import { cn } from "@/lib/utils";
import {
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
  navigationMenuTriggerStyle,
} from "@/components/ui/navigation-menu";
import { usePathname } from "next/navigation";
import { buttonVariants } from "../ui/button";

const navData: { title: string; href: string }[] = [
  {
    title: "Home",
    href: "/",
  },
  {
    title: "Courses",
    href: "/courses",
  },
  {
    title: "About Us",
    href: "/about",
  },
  {
    title: "Pricing",
    href: "pricing",
  },
  {
    title: "Contact",
    href: "/contact",
  },
];

export function NavigationBar() {
  const path = usePathname();
  console.log(path);
  return (
    <div className="container mx-auto py-6 space-x-12 flex items-center">
      <Link
        href={"/"}
        className="flex items-center justify-center size-14 bg-primary rounded-lg"
      >
       
      </Link>
      <NavigationMenu>
        <NavigationMenuList>
          <NavigationMenuItem>
            {navData.map((item) => (
              <Link key={item.href} href={item.href} legacyBehavior passHref>
                <NavigationMenuLink
                  className={cn(
                    navigationMenuTriggerStyle(),
                    "h-14 w-auto px-6 text-lg"
                  )}
                  active={path.includes(item.href)}
                >
                  {item.title}
                </NavigationMenuLink>
              </Link>
            ))}
          </NavigationMenuItem>
        </NavigationMenuList>
      </NavigationMenu>
      <div className="mx-auto">
        <Link
          href="/"
          className={buttonVariants({ variant: "ghost", size: "lg" })}
        >
          Sign In
        </Link>

        <Link
          href="/"
          className={buttonVariants({ variant: "default", size: "lg" })}
        >
          Sign Up
        </Link>
      </div>
    </div>
  );
}
